import React from 'react';
import styled from '@emotion/styled';
import useMeasure from '@kevinastone/use-measure';
import {animated, config, useTransition} from 'react-spring';

function AccordianItem({opacity, height, className, children}) {
  const [bind, {height: viewHeight}] = useMeasure();

  return (
    <animated.div
      className={className}
      style={{
        opacity,
        height: height.interpolate({output: [0, viewHeight]}),
      }}
    >
      <animated.div {...bind}>{children}</animated.div>
    </animated.div>
  );
}

const StyledAccordianItem = styled(AccordianItem)`
  width: 100%;
`;

export default function useAccordian(items, keyFunc) {
  const transitions = useTransition(items, keyFunc, {
    from: {opacity: 0, height: 0},
    enter: {opacity: 1, height: 1},
    leave: {opacity: 0, height: 0},
    trail: 50,
    config: config.gentle,
  });

  return callback =>
    transitions.map(({key, item, props}, index) => (
      <StyledAccordianItem key={key} {...props}>
        {callback(item, index)}
      </StyledAccordianItem>
    ));
}
